/* Paragraph styling */
p {
  margin-bottom: 13px; /* Adds space below the <p> element */
  font-size: 14px;
  color: #333;
}

/* Label styling */
label {
  display: block;
  margin-bottom: 13px;
  font-size: 14px;
  color: #333;
}

/* Checkbox styling */
input[type="checkbox"] {
  margin-right: 10px;
}

/* Button styling */
button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

/* Checkbox group styling */
.checkbox-group {
  margin-bottom: 20px;
}

.checkbox-group label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

/* Single-line heading styling */
.service-type-label {
  font-size: 17px;
  font-weight: bold;
  color: #000;
  margin-bottom: 20px;
  white-space: nowrap; /* Ensures text stays in a single line */
}

/* Checkbox label styling */
.checkbox-label {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

/* Button container styling */
.button-container {
  display: flex;
  justify-content: flex-end; /* Aligns the button to the right */
}